import React, { Component } from 'react'
import {Link} from 'react-router-dom'
export default class Navigation extends Component {

    constructor(props) {
        super(props);
        this.state = {
            isExpanded: false,
            isHidden: true,
            barsIsClicked: false
        };
    }
    handleToggle = (e) => {
        e.preventDefault();

        const { barsIsClicked, isExpanded, isHidden } = this.state;

        this.setState(() => ({ isExpanded: !isExpanded, isHidden: !isHidden, barsIsClicked: !barsIsClicked }));
    }

    render() {
        const { isExpanded, isHidden } = this.state;

        const hidden = isHidden ? 'hidden' : '';
        const expanded = isExpanded ? 'hide-line' : '';

        return (
            <nav >


                <div className={`barsIsClicked `} >
                    <div id='bars' className={`fa fa-bars`} aria-hidden="true" onClick={(e) => { this.handleToggle(e) }}></div>

                    <ul className={`${isExpanded ? 'is-expanded' : ''}`} >
                        <li className={hidden}>
                           <Link to='/info'>
                            Information
                           </Link>
                        </li>
                        <li className={`${hidden} ' ' ${expanded}`}>|

                        </li>
                        <li className={hidden}>
                            about us
                        </li>
                        <li className={`${hidden} ' ' ${expanded}`}>|

                        </li>
                        <li className={hidden}>
                            contact us
                        </li>
                    </ul>
                </div>


            </nav>
        )
    }
}
