import React, { Component } from 'react'
import woodTick from '../images/wood-tick.jpg'
import deerTick from '../images/deer-tick.jpg'
import mosquito from '../images/mosquito.jpg'

export default class InfoList extends Component {
    constructor(props) {
        super(props);
        this.state = {
            ticksExpanded: true,
            mosquitosExpanded: false,
            spidersExpanded: true
        };
    }
    handleToggle = (e) => {
        e.preventDefault();
        const { textContent } = e.target.attributes.value;
        const { ticksExpanded, mosquitosExpanded, spidersExpanded } = this.state;

        switch (textContent) {
            case 'tick':
                this.setState(() => ({ ticksExpanded: !ticksExpanded }));
                break;
            case 'mosquito':
                this.setState(() => ({ mosquitosExpanded: !mosquitosExpanded }));
                break;
            case 'spider':
                this.setState(() => ({ spidersExpanded: !spidersExpanded }));
                break;
            default:
                this.setState(() => ({ ticksExpanded, mosquitosExpanded, spidersExpanded }));
        }

    }
    render() {





        const { ticksExpanded, mosquitosExpanded, spidersExpanded } = this.state;
        const cNameHidden = 'bug-card hidden';
        const cName = 'bug-card';
        const ticksExpandedHidden = ticksExpanded ? cNameHidden : cName;
        const mosquitosExpandedHidden = mosquitosExpanded ? cNameHidden : cName;
        const spidersExpandedHidden = spidersExpanded ? cNameHidden : cName;
        return (
            <div className='info-list'>


                <h2 value='mosquito' className='bug-header' onClick={this.handleToggle}>{`${mosquitosExpanded ? '+' : '-'} Mosquitoes*`}</h2>
                <ul >
                    <li className={mosquitosExpandedHidden}>
                        <h2>Aedes vexans</h2> <h2>(Commmon Mosquito)</h2>
                        <img src={mosquito} alt="Wood Tick" />
                        <h3>Carried Diseases</h3>
                        <ul>
                            <li>
                                <h4>Francisella tularensis</h4> <h4>(tularemia)</h4>
                            </li>
                            <li>
                                <h4>Rickettsia rickettsii</h4> <h4>(Rocky Mountain spotted fever)</h4>
                            </li>
                        </ul>
                    </li>


                </ul>


                <h2 value='spider' className='bug-header' onClick={this.handleToggle}>{`${spidersExpanded ? '+' : '-'} Spiders*`}</h2>
                <ul >
                    <li className={spidersExpandedHidden}>
                        <h2>American Dog Tick</h2> <h2>(Wood Tick)</h2>
                        <img src={woodTick} alt="Wood Tick" />
                        <h3>Carried Diseases</h3>
                        <ul>
                            <li>
                                <h4>Francisella tularensis</h4> <h4>(tularemia)</h4>
                            </li>
                            <li>
                                <h4>Rickettsia rickettsii</h4> <h4>(Rocky Mountain spotted fever)</h4>
                            </li>
                        </ul>
                    </li>


                </ul>
                <h2 value='tick' className='bug-header' onClick={this.handleToggle}>{`${ticksExpanded ? '+' : '-'} Ticks**`}</h2>
                <ul >
                    <li className={ticksExpandedHidden}>
                        <h2>American Dog Tick</h2> <h2>(Wood Tick)</h2>
                        <img src={woodTick} alt="Wood Tick" />
                        <h3>Carried Diseases</h3>
                        <ul>
                            <li>
                                <h4>Francisella tularensis</h4> <h4>(tularemia)</h4>
                            </li>
                            <li>
                                <h4>Rickettsia rickettsii</h4> <h4>(Rocky Mountain spotted fever)</h4>
                            </li>
                        </ul>
                    </li>

                    <li className={ticksExpandedHidden}>
                        <h2>Blacklegged Tick </h2> <h2>(Deer Tick)</h2>
                        <img src={deerTick} alt="Deer Tick" />
                        <h3>Carried Diseases</h3>
                        <ul>
                            <li>
                                <h4>Borrelia burgdorferi and B. mayonii</h4> <h4>(which cause Lyme disease)</h4>
                            </li>
                            <li>
                                <h4>Anaplasma phagocytophilum</h4> <h4>(anaplasmosis)</h4>
                            </li>
                            <li>
                                <h4>B. miyamotoi disease</h4> <h4>(a form of relapsing fever)</h4>
                            </li>
                            <li>
                                <h4>Ehrlichia muris eauclairensis</h4> <h4>(ehrlichiosis)</h4>
                            </li>
                            <li>
                                <h4>Babesia microti</h4> <h4>(babesiosis)</h4>
                            </li>
                            <li>
                                <h4>Powassan virus</h4> <h4>(Powassan virus disease)</h4>
                            </li>
                        </ul>
                    </li>
                </ul>

                <p className='disclaimer'>* All information was obtained from the Bug guide website
                for more information please refer to their site at <a href="https://bugguide.net/node/view/57572">bugguide.net</a>
                </p>
                <p className='disclaimer'>** All information was obtained from the Centers for Disease Control's website
                for more information please refer to their site at <a href="https://www.cdc.gov">www.cdc.gov</a>
                </p>
            </div>
        )
    }
}
